city: 0.5;\r\n      background: $purple;\r\n   }\r\n}\r\n\r\n.button {\r\n   background: $purple;\r\n   border: none;\r\n   border-bottom: 0.3rem solid darken($purple, 10%);\r\n   color: $dark-text;\r\n   font-weight: 500;\r\n   border-radius: 5px;\r\n   padding: $s-size;\r\n   transition: background 0.5s ease;\r\n\r\n   &:hover {\r\n      background: lighten($purple, $amount: 10%);\r\n   }\r\n\r\n   &:active {\r\n      background: darken($purple, $amount: 10%);\r\n      transition: none;\r\n   }\r\n}\r\n\r\n.button--link {\r\n   background: none;\r\n   border: none;\r\n   color: $off-white;\r\n   font-weight: 500;\r\n   padding: 0;\r\n   height: 25px;\r\n   border-bottom: 1px solid transparent;\r\n   transition: all 0.5s ease;\r\n\r\n   &:hover {\r\n      color: white;\r\n      border-bottom: 1px solid white;\r\n   }\r\n\r\n   &:active {\r\n      color: rgb(167, 167, 167);\r\n      border-bottom: 1px solid rgb(167, 167, 167);\r\n      transition: none;\r\n   }\r\n}\r\n\r\n@media (max-width: $mobile-breakpoint) {\r\n   .big-button {\r\n      font-size: 2.5rem;\r\n      margin-bottom: $l-size;\r\n   }\r\n}\r\n",".container {\r\n    max-width: 60rem;\r\n    margin: 0 auto;\r\n    padding: 0 $m-size;\r\n\r\n    & > &-box {\r\n        background: $off-black;\r\n        padding: 5rem 3rem 4rem 3rem;\r\n        border-radius: 10px;\r\n        box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);\r\n        margin-bottom: 5rem;\r\n    }\r\n}",".header {\r\n   background: $off-black;\r\n   color: white;\r\n   margin-bottom: $xl-size;\r\n   padding: $m-size 0;\r\n\r\n   &__title {\r\n      font-size: $l-size;\r\n      margin: 0;\r\n      padding-bottom: 0.5rem;\r\n   }\r\n\r\n   &__subtitle {\r\n      color: $off-white;\r\n      font-size: $m-size;\r\n      font-weight: 500;\r\n      margin: 0;\r\n   }\r\n}\r\n\r\n@media (max-width: $mobile-breakpoint) {\r\n   .header {\r\n      margin-bottom: $l-size;\r\n   }\r\n}",".ReactModalPortal {\r\n\r\n   & > div {\r\n      opacity: 0;\r\n   